<template>
    <div>
        <van-nav-bar
            title="安全中心"
            left-text="返回" 
            right-text="退出登入"
            @click-left="toback"
            @click-right="outLogin"
            :fixed="true"
            />
            <div  class="mtop">
                <van-cell-group>
                    <van-cell  title="修改密码" is-link @click="changePsw" />
                    <van-cell   title="注销账号" is-link @click="endAll" />
                </van-cell-group>
            </div>
        
    </div>
</template>

<script>
import { Dialog } from 'vant';
    export default {
        data(){
            return{

            }
        },
        methods:{
            toback(){
                this.$router.go(-1)
            },
            outLogin(){
                localStorage.removeItem("login_Tk");
                this.$router.push({ name: "Login" });
            },
            changePsw(){
                this.$router.push({name:'Changepsw'})
            },
            endAll(){
                Dialog.confirm({
                    title: '注销账号',
                    message: '一旦注销，数据无法恢复',
                })
                .then(() => {
                    let tokenString = localStorage.getItem("login_Tk");

                    this.$toast.loading({
                        message: "加载中...",
                        forbidClick: true,
                        duration: 0
                    });

                    this.axios({
                        method: "POST",
                        url: "/destroyAccount",
                        data: {
                        appkey: this.appkey,
                        tokenString
                        }
                    })
                    .then(result => {
                        this.$toast.clear();
                        

                        if (result.data.code == 'G001') {
                            //清除token
                            localStorage.removeItem('__Tk');
                            this.$router.push({name: 'Login'});
                        } else {
                            this.$toast(result.data.msg);
                        }
                    })
                    .catch(err => {
                        this.$toast.clear();
                        
                    });
                })
                .catch(() => {
                    
                });
            }
        }
    }
</script>

<style lang="less" scoped>
.mtop{
    margin-top: 50px;
}
</style>